import inputCell from '@/components/input-cell'
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
import cityData from '@/common/js/city.data.js';
import { addAddress, delAddress, editAddress, getAddressDetail } from '@/api'
import { mapState, mapActions } from 'vuex'
export default {
	name: '',
	data () {
		return {
			isDefault: 0,
			id: '', // 地址id
			name: '',
			phone: '',
			postcode: '',
			address: '',
			province: '',
			city: '',
			district: '',
			detail: '',
			token: ''
		}
	},
	onLoad (options) {
		this.token = uni.getStorageSync('token')
		if(options.id) {
			this.id = options.id
			this._echoContent()
			this._setNavigationBarButton()
		}
	},
	onNavigationBarButtonTap () {
		if(this.id) {
			this._delAddress()
		}
	},
	computed: {
		fixTop () {
			return (uni.getSystemInfoSync().windowHeight - 70) + 'px'
		}
	},
	methods: {
		...mapActions(['reqGetAddressList']),
		async _echoContent () {
			let result = await getAddressDetail ({
				address_id: this.id,
				token: this.token
			})
			this.name = result.data.name;
			this.phone = result.data.phone;
			this.postcode = result.data.zipcode;
			this.address = result.data.province + '-' + result.data.city + '-' + result.data.district;
			this.province = result.data.province;
			this.city = result.data.city;
			this.district = result.data.district;
			this.detail = result.data.detailed;
			this.isDefault = result.data.default;
		},
		async _delAddress () {
			let self = this;
			uni.showModal({
				content: '确定要删除吗?',
				success: async function (res) {
					if(res.confirm) {
						let result = await delAddress ({
							token: self.token,
							aid: self.id
						})
						if(result.code == 2000) {
							uni.showToast({
								title: '删除成功'
							})
							setTimeout(() => {
								uni.navigateBack();
								self.reqGetAddressList({
									token: self.token
								})
							}, 1500)
						}
					}
				}
			})
		},
		async onSaveAddressClick () {
			let self = this;
			if(this.name == '') {
				uni.showToast({
					title: '请输入联系人姓名',
					icon: 'none'
				})
				return
			}
			if(this.phone == '') {
				uni.showToast({
					title: '请输入联系人手机',
					icon: 'none'
				})
				return
			}
			if(this.postcode == '') {
				uni.showToast({
					title: '请输入邮政编码',
					icon: 'none'
				})
				return
			}
			if(this.address == '') {
				uni.showToast({
					title: '请输入收货地址',
					icon: 'none'
				})
				return
			}
			let result;
			let obj = {
				token: this.token,
				aid: this.id || '',
				province: this.province,
				city: this.city,
				district: this.district,
				detailed: this.detail,
				name: this.name,
				zipcode: this.postcode,
				phone: this.phone,
				default: this.isDefault
			}
			if(this.id) {
				result = await editAddress (obj)
			}else {
				result = await addAddress (obj)
			}
			if(result.code == 2000) {
				uni.showToast({
					title: '保存成功'
				})
				setTimeout(() => {
					self.reqGetAddressList ({
						token: self.token
					})
					uni.navigateBack();
				}, 1500)
			}
		},
		onConfirm (e) {  // 确定选择
			console.log(e)
			this.address = e.label;
			this.province = e.label.split('-')[0]
			this.city = e.label.split('-')[1]
			this.district = e.label.split('-')[2]
		},
		onSetDefault () {
			this.isDefault = this.isDefault == 0 ? 1 : 0;
		},
		onInputClick () {  // 选择省市区
			this.$refs.mpvueCityPicker.show()
		},
		_setNavigationBarButton () {
			let self = this;
			let pages = getCurrentPages();
			let page = pages[pages.length - 1];













		}
	},
	components: {
		inputCell,
		mpvueCityPicker
	}
}
